@charset "UTF-8";
*::after,
*::before {
  box-sizing: border-box; }

body {
  min-width: 320px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: normal; }

.visually-hidden:not(:focus):not(:active) {
  /*Позиционирование*/
  position: absolute;
  /*Блочная модель*/
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  /*Разное*/
  white-space: nowrap;
  clip-path: inset(100%);
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* montserrat-regular - latin */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/montserrat-v14-latin-regular.woff2") format("woff2"), url("../fonts/montserrat-v14-latin-regular.woff") format("woff"); }

/* montserrat-500 - latin */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: local("Montserrat Medium"), local("Montserrat-Medium"), url("../fonts/montserrat-v14-latin-500.woff2") format("woff2"), url("../fonts/montserrat-v14-latin-500.woff") format("woff"); }

/* montserrat-600 - latin */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat SemiBold"), local("Montserrat-SemiBold"), url("../fonts/montserrat-v14-latin-600.woff2") format("woff2"), url("../fonts/montserrat-v14-latin-600.woff") format("woff"); }

.page-header {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 1366px;
  height: 650px;
  margin: 0 auto;
  background-image: url("../img/preview-desctop.png");
  background-repeat: no-repeat;
  background-size: 1366px 650px; }

.page-header__preview {
  position: absolute;
  content: "";
  top: 215px;
  left: 200px;
  display: block;
  color: #F9FBFD; }

.page-header__preview-title {
  margin-bottom: 0;
  font-weight: bold;
  font-size: 54px;
  line-height: 66px;
  letter-spacing: 3.2px; }

.page-header__preview-text {
  margin-top: 11px;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 1.65px; }

.page-header__logo {
  display: block;
  width: 77px;
  height: 38px;
  padding-left: 103px;
  padding-top: 35px; }

.main-nav {
  display: block; }

.main-nav__toggle {
  display: none;
  width: 24px;
  height: 12px;
  background-image: url("../img/Burger.svg");
  background-repeat: no-repeat;
  background-size: 24px 12px;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent; }

.main-nav__wrapper {
  display: block;
  padding-top: 45px;
  padding-right: 70px; }

.main-nav__list {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0;
  list-style: none; }

.main-nav__item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-right: 33px; }

.main-nav__item-elem {
  display: block;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
  color: #F9FBFD;
  letter-spacing: 1.5px; }

.preview {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: nowrap; }

.preview-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-wrap: nowrap;
  margin-top: 154px;
  margin-left: 103px; }

.preview-wrapper__title {
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 150%; }

.preview-wrapper__text {
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 150%;
  /* or 24px */ }

.preview-wrapper__img {
  padding-top: 120px;
  padding-bottom: 120px; }
